body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    padding-top:120px;
    font-size:14px;
}

.App--loading {
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:center;
            justify-content:center;
    -webkit-align-items:center;
            align-items:center;
    height:100%;
}

.Contents {
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:space-around;
            justify-content:space-around;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
    margin:0 auto;
    padding:50px;
    max-width:1200px;
}
.Header {
    position:fixed;
    top:0;
    left:0;
    z-index:100;
    padding:20px 0 30px;
    width:100%;
    background:#555;
    text-align:center;
}
.ProjectTitle {
    color:#fff; 
    font-size:26px;
}
.Sort a {
    margin:0 8px;
    color:#e1e1e1;
}
.Sort .Active {
    color:#fff;
    font-weight:bold;
}
.MovieCard{
    background-color:white;
    width:40%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items:flex-start;
            align-items:flex-start;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
    margin-bottom:50px;
    text-overflow: ellipsis;
    padding:0 20px;
    box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133,0.22);
}

.Movie__Column{
    width:30%;
    box-sizing:border-box;
    text-overflow: ellipsis;
}

.Movie__Column:last-child{
    padding:20px 0;
    width:60%;
}

.MovieCard h1{
    font-size:20px;
    font-weight: 600;
}

.MovieCard .Movie__Genres{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
    margin-bottom:20px;
}

.Movie__Genres .Movie__Genre{
    margin-right:10px;
    color:#B4B5BD;
}

.MovieCard .Movie__Synopsis {
    text-overflow: ellipsis;
    color:#B4B5BD;
    overflow: hidden;
}

.MovieCard .Movie__Poster{
    max-width: 100%;
    position: relative;
    top:-20px;
    box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80,80,80,0.22);
}

@media screen and (min-width:320px) and (max-width:667px){
    .MovieCard{
        width:100%;
    }
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait){
    .MovieCard{
        width:100%;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .Movie__Poster{
        top:0;
        left:0;
        width:100%;
    }
    .Movie__Column{
        width:100%!important;
    }
}
